)}.burger-menu{position:fixed;top:1.5rem;right:2rem;cursor:pointer;display:flex;flex-direction:column;gap:5px;z-index:1001;padding:.5rem}@media(min-width: 769px){.burger-menu{display:none}}.burger-bar{width:28px;height:2px;background-color:#fff;border-radius:2px;transition:all .3s cubic-bezier(0.4, 0, 0.2, 1)}.burger-menu:hover .burger-bar{background-color:#667eea}.nav-menu{position:fixed;top:0;right:0;height:100%;width:100%;max-width:320px;background:rgba(15,15,30,.95);backdrop-filter:blur(40px);border:none;box-shadow:-4px 0 24px rgba(0,0,0,.3);display:flex;flex-direction:column;align-items:center;justify-content:center;gap:2rem;transform:translateX(100%);transition:transform .4s cubic-bezier(0.4, 0, 0.2, 1);z-index:1000}.nav-menu.open{transform:translateX(0)}.nav-menu a{font-size:1.5rem;font-weight:600;color:#fff;padding:.5rem 1rem;border-radius:8px;transition:all .3s cubic-bezier(0.4, 0, 0.2, 1)}.nav-menu a:hover{background:rgba(102,126,234,.2);color:#667eea}@media(min-width: 769px){.nav-menu{position:static;flex-direction:row;max-width:none;width:auto;background:rgba(0,0,0,0);backdrop-filter:none;transform:none;gap:1rem}.nav-menu a{font-size:1rem}}.languages-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(180px, 1fr));gap:2rem;max-width:1200px;margin:0 auto}.language-box{background:rgba(255,255,255,.05);backdrop-filter:blur(20px);border:1px solid rgba(255,255,255,.1);box-shadow:0 8px 32px rgba(0,0,0,.1);padding:3rem 2rem;border-radius:24px;display:flex;flex-direction:column;align-items:center;justify-content:center;aspect-ratio:1;transition:all .3s cubic-bezier(0.4, 0, 0.2, 1)}.language-box:hover{transform:translateY(-8px) scale(1.02);border-color:rgba(255,255,255,.3);box-shadow:0 16px 48px rgba(102,126,234,.2)}.language-logo{width:100%;height:auto;max-width:120px;max-height:120px;object-fit:contain;margin-bottom:1rem;filter:drop-shadow(0 4px 8px rgba(0, 0, 0, 0.1))}.tooltip{font-size:.9rem;font-weight:600;color:#fff;text-align:center}sectio