.projects-container{display:grid;grid-template-columns:repeat(auto-fit, minmax(min(350px, 100%), 1fr));justify-content:center;gap:2rem;max-width:1600px;margin:0 auto;width:100%}.project-card{background:rgba(255,255,255,.05);backdrop-filter:blur(20px);border:1px solid rgba(255,255,255,.1);padding:2rem;border-radius:24px;width:100%;text-align:left;box-shadow:0 8px 32px rgba(0,0,0,.1);transition:all .3s cubic-bezier(0.4, 0, 0.2, 1);position:relative;overflow:hidden}.project-card::before{content:"";position:absolute;top:0;left:0;right:0;height:4px;background:linear-gradient(90deg, #667eea 0%, #764ba2 100%);transform:scaleX(0);transform-origin:left;transition:transform .3s ease}.project-card:hover{transform:translateY(-8px);border-color:rgba(255,255,255,.2);box-shadow:0 16px 48px rgba(0,0,0,.2)}.project-card:hover::before{transform:scaleX(1)}.project-card h3{color:#fff;margin-bottom:1rem;font-size:clamp(1.25rem,2.5vw,1.5rem);font-weight:600;line-height:1.3}.project-card a{display:inline-flex;align-items:center;gap:.5rem;color:#667eea;text-decoration:none;font-weight:600;font-size:1rem;padding:.75rem 1.5rem;border-radius:12px;background:rgba(102,126,234,.1);border:1px solid rgba(102,126,234,.2);transition:all .3s ease;margin-top:1rem}.project-card a:hover{background:rgba(102,126,234,.2);border-color:rgba(102,126,234,.4);transform:translateX(4px)}.project-card a::after{content:"→";font-size:1.2rem;transition:transform .3s ease}.project-card a:hover::after{transform:translateX(4px)}.project-card .repo-description{color:#b8b9c9;margin:1rem 0;font-size:.95rem;line-height:1.7}.project-card .repo-description h1,.project-card .repo-description h2,.project-card .repo-description h3,.project-card .repo-description h4,.project-card .repo-description h5,.project-card .repo-description h6{color:#fff;margin:1rem 0 .5rem;font-weight:600}.project-card .repo-description p{color:#b8b9c9;margin:.75rem 0;line-height:1.7}.project-card .repo-description a{color:#667eea;text-decoration:none;border-bottom:1px solid rgba(102,126,234,.3);transition:all .3s ease}.project-card .repo-description a:hover{color:#5568d3;border-bottom-color:#5568d3}.project-card .repo-description code{color:#f093fb;background:rgba(240,147,251,.1);padding:.2rem .4rem;border-radius:4px;font-size:.9em;font-family:"Courier New",monospace}.project-card .repo-description pre{background:rgba(0,0,0,.3);color:#e0e0e0;padding:1rem;border-radius:12px;overflow-x:auto;margin:1rem 0;border:1px solid rgba(255,255,255,.1)}.project-card .repo-description pre code{background:none;padding:0;color:inherit}.project-card .repo-description blockquote{border-left:3px solid #667eea;padding-left:1rem;margin:1rem 0;color:#b8b9c9;font-style:italic}.project-card .repo-description ul,.project-card .repo-description ol{padding-left:1.5rem;margin:.75rem 0}.project-card .repo-description ul li,.project-card .repo-description ol li{margin:.5rem 0;color:#b8b9c9}.project-card .project-languages{margin-top:1.5rem;padding-top:1.5rem;border-top:1px solid rgba(255,255,255,.1)}.project-card .project-languages .languages-bar{display:flex;flex-wrap:wrap;gap:.5rem}.project-card .project-languages .language-bar-wrapper{display:inline-block}.project-card .project-languages .language-name{font-size:.85rem;font-weight:600;color:#fff;padding:.5rem 1rem;background:rgba(102,126,234,.15);border:1px solid rgba(102,126,234,.3);border-radius:20px;display:inline-block;transition:all .3s ease}.project-card .project-languages .language-name:hover{background:rgba(102,126,234,.25);border-color:rgba(102,126,234,.5);transform:translateY(-2px)}.project-card .project-languages .language-bar{display:none}@media(max-width: 1024px){.projects-container{grid-template-columns:repeat(auto-fit, minmax(min(280px, 100%), 350px));gap:1.5rem;padding:1.5rem 1rem}}@media(max-width: 768px){.projects-container{grid-template-columns:1fr;gap:1.5rem;padding:1rem}.project-card{padding:1.5rem;max-width:600px;margin:0 auto}}@media(max-width: 480px){.projects-container{padding:.75rem .5rem;gap:1rem}.project-card{padding:1rem;border-radius:12px}.project-languages .language-bar{padding:0 .5rem}}